import React, { Component } from "react";
import API from "../services";

export default class NavbarEmployee extends Component {

  constructor(){
    super();
    this.state={
      namaUser:null,
      email:null,
      phone_number:null,
      address:null,
      status:null,
      username:null,
      category_id:null,
      caUser:null,
    }
  }

  UNSAFE_componentWillMount(){
    this.getUserLogin();


  }
  getUserLogin(){
    API.GetUserLogin().then(res=>{
      const dataUser=res.data;
      // console.log(dataUser);
      let nameUser=dataUser.user_detail.fullname;
      let email=dataUser.email;
      let phone=dataUser.user_detail.phone_number;
      let address=dataUser.user_detail.address;
      let status=dataUser.user_detail.activate;
      let username=dataUser.username;
      let category_id=dataUser.category_access_id;
      
      this.setState({
        namaUser:nameUser,
        email:email,
        phone_number:phone,
        address:address,
        status:status,
        username:username,
        category_id:category_id,
        
      })
      // console.log(this.state.category_id);
      API.GetCaUser(category_id).then(res=>{
        const Ca=res.data.data;
        let caUser=Ca.name;
        
        this.setState({
          caUser:caUser
        })
      })
    });
    
  }

  render() {
    return (
      <nav className="navbar default-layout col-lg-12 col-12 p-0 fixed-top d-flex flex-row">
        <div className="text-center navbar-brand-wrapper d-flex align-items-top justify-content-center">
          <a className="navbar-brand brand-logo" href="url">
            <img src="../assets/images/logo.svg" alt="logo" />{" "}
          </a>
          <a className="navbar-brand brand-logo-mini" href="index.html">
            <img src="../assets/images/logo-mini.svg" alt="logo" />{" "}
          </a>
        </div>
        <div className="navbar-menu-wrapper d-flex align-items-center">
          <ul className="navbar-nav">
            <li className="nav-item font-weight-semibold ">
              Dashboard administrasi 
              {/* <span class="text-warning"> administrasi</span> */}
            </li>
          </ul>
          <form className="ml-auto search-form d-none d-md-block" action="url">
            <div className="form-group">
              <input
                type="search"
                className="form-control"
                placeholder="Search Here"
              />
            </div>
          </form>
          <ul className="navbar-nav ml-auto">
            <li className="nav-item dropdown user-dropdown">
              <a
                className="nav-link dropdown-toggle"
                id="UserDropdown"
                href="url"
                data-toggle="dropdown"
              >
                <img
                  className="img-xs rounded-circle"
                  src="../assets/images/medi.jpg"
                  alt="img"
                />{" "}
              </a>
              <div
                className="dropdown-menu dropdown-menu-right navbar-dropdown"
                aria-labelledby="UserDropdown"
              >
                <div className="dropdown-header text-center">
                  <img
                    className="img-md rounded-circle"
                    src="../assets/images/medi.jpg"
                    alt="img"
                  />
  <p className="mb-1 mt-2 font-weight-semibold">{this.state.namaUser}</p>
                  <p className="font-weight-light text-muted mb-0">
                    {this.state.email}
                  </p>
                </div>
                <div className="text-center mt-0">
                  <label className="badge badge-info">{this.state.caUser}</label>
                </div>
                <a href="url" className="dropdown-item">
                  My Profile
                  <i className="dropdown-item-icon ti-dashboard" />
                </a>
                <a href="../index.html" className="dropdown-item">
                  Logout
                  <i className="dropdown-item-icon ti-power-off" />
                </a>
              </div>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}
