import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class MenuEmployee extends Component {

  setBtnActivate(e) {
    e.target.classList.remove("btn-secondary")
    e.target.classList.add("btn-primary")
  }

  render() {
    return (
      <div>
        <div className="row page-title-header">
          <div className="col-md-12 text-center">
            <div className="page-header-toolbar">
              <div
                className="btn-group toolbar-item"
                role="group"
                aria-label="Basic example"
              >
                <Link to="/EmployeListJob" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary mr-2">
                  Daftar Pekerjaan
                </Link>
              </div>
              <div className="btn-group toolbar-item">
                <Link to="/EmployeeHistoryJob" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary mr-2">
                  History Pekerjaan
                </Link>
              </div>
              <div className="btn-group toolbar-item">
                <Link to="AdmisAddJob" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary mr-2">
                  Tambah Pekerjaan
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
