import React, { Component } from "react";
import API from "./services";

export default class Login extends Component {

  constructor(props){
    super(props);
    this.state = {
      username:"",
      password:"",
      err:null
    }
  }

  Login(){
    const { history } = this.props;
    API.Login({"username" : this.state.username,"password": this.state.password}).then(res =>{
      localStorage.setItem('token', res.data.token);
      if (res.data.ca==='root') {
        history.push("/Dashboard")
      }
      else{
        history.push("/EmployeListJob")
      }
      
      // history.push
    }).catch(err => {
      localStorage.removeItem('token')
      this.setState({
        err:err.data.data
      })
    })
  }

  render() {
    return (
      <div className="container-scroller">
        <div className="container-fluid page-body-wrapper full-page-wrapper">
          <div className="content-wrapper d-flex align-items-center auth register-bg-1 theme-one">
            <div className="row w-100">
              <div className="col-lg-4 mx-auto">
                <div className="auto-form-wrapper">
                  <p className="text-danger text-center">
                  {this.state.err ? this.state.err : ''}
                  </p>
                  <form>
                    <div className="form-group">
                      <label className="label">Username</label>
                      <div className="input-group">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Username"
                          value={this.state.username}
                          onChange={(e)=>{this.setState({username:e.target.value})}}
                        />
                        <div className="input-group-append">
                          <span className="input-group-text">
                            <i className="mdi mdi-check-circle-outline" />
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <label className="label">Password</label>
                      <div className="input-group">
                        <input
                          type="password"
                          className="form-control"
                          placeholder="*********"
                          value={this.state.password}
                          onChange={(e)=>{this.setState({password:e.target.value})}}
                        />
                        <div className="input-group-append">
                          <span className="input-group-text">
                            <i className="mdi mdi-check-circle-outline" />
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <button
                        className="btn btn-primary submit-btn btn-block"
                        id="btnLogin"
                        onClick={(e)=>{
                          e.preventDefault();
                          this.Login()
                        }}
                      >
                        Login
                      </button>
                    </div>
                    <div className="form-group d-flex justify-content-between">
                      <div className="form-check form-check-flat mt-0">
                        <label className="form-check-label">
                          <input
                            type="checkbox"
                            className="form-check-input"
                            defaultChecked
                          />{" "}
                          Keep me signed in{" "}
                        </label>
                      </div>
                      <a
                        href="url"
                        className="text-small forgot-password text-black"
                      >
                        Forgot Password
                      </a>
                    </div>
                    <div className="form-group">
                      <button className="btn btn-block g-login">
                        <img
                          className="mr-3"
                          src="assets/images/google.svg"
                          width={20}
                          height={20}
                          alt="img"
                        />
                        Log in with Google
                      </button>
                    </div>
                  </form>
                </div>
                <ul className="auth-footer">
                  <li>
                    <a href="url">Conditions</a>
                  </li>
                  <li>
                    <a href="url">Help</a>
                  </li>
                  <li>
                    <a href="url">Terms</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
       
      </div>
    );
  }
}
