import React, { Component } from "react";
import Navbar from "../includes/Navbar";
import Menu from "../includes/Menu";
import Footer from "../includes/Footer";
import API from "../services";

export default class AddJob extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list_category: null,
      name: "",
      description: "",
      start_time: "",
      deadline: "",
      done: false,
      job_location: "",
      no_spk: "",
      given_by: "",
      nilai_jasa: "",
      nilai_material: "",
      category_id: "",
      name_jobCa:"",
      List_user:null,
      user_id:""
    };
  }

  UNSAFE_componentWillMount() {
    if (!localStorage.getItem("token")) {
      console.log("belum login");
    }
    this.getJobCa();
    this.getUser();
  }
  getJobCa() {
    API.GetJobCa().then(res => {
      if (res.data) {
        const data = res.data.data.map(item => (
          <option key={item.id} value={item.id}>
            {item.name}
          </option>
        ));

        this.setState({
          list_category: data
        });
      } else {
        console.log("error");
      }
    });
  }

  getUser(){
    API.GetUserList().then(res=>{
      if (res.data) {
        const Userdata=res.data.data.map(itemUser=>(
          <option key={itemUser.user_detail.id} value={itemUser.id}>
            {itemUser.user_detail.fullname}
          </option>
          // console.log(itemUser.user_detail.id)
        ));
        this.setState({
          List_user:Userdata
        })
      }else{
        console.log("error")
      }
    })
  }

  addJob() {
    let data = {
      name: this.state.name,
      description: this.state.description,
      start_time: this.state.start_time
        .split("-")
        .reverse()
        .join("-"),
      deadline: this.state.deadline
        .split("-")
        .reverse()
        .join("-"),
      job_location: this.state.job_location,
      no_spk: this.state.no_spk,
      nilai_jasa: this.state.nilai_jasa,
      nilai_material: this.state.nilai_material,
      given_by: this.state.given_by,
      category_id: Number(this.state.category_id),
      done: false,
      users:this.state.user_id,
    };
    API.AddJob(data)
      .then(res => {
        // console.log("berhasil")
        // this.props.history.push('/ListJob)
        this.props.history.push('/ListJob')
      })
      .catch(err => {
        console.log(err);
      });
  }

  postJobCa(){
    let data_jobCa={
      name:this.state.name_jobCa
    }
    if(this.state.name_jobCa.length !== 0){
      API.PostJobCa(data_jobCa).then(res=>{
        this.getJobCa();
        document.getElementById('exampleModalCenter').classList.remove("show");
        document.getElementById('exampleModalCenter').style.display = 'none';
        document.getElementById('exampleModalCenter').removeAttribute('aria-modal');
        document.getElementById('exampleModalCenter').setAttribute('aria-hidden','true')
        document.getElementsByClassName('modal-backdrop')[0].classList.remove("show");
        document.getElementsByClassName('modal-backdrop')[0].remove()
      })
      .catch(err => {
          console.log("error");
          console.log(err)
        });
    }else{
      alert("tak boleh kosong geng")
    }
  }

  render() {
    return (
      <div>
        <Navbar />
        <div className="container-fluid page-body-wrapper">
          <div className="container-fluid">
            <div className="content-wrapper">
              <Menu />
              <hr />
              <div className="row">
                <div className="col-md-9 d-flex align-items-stretch grid-margin mx-auto">
                  <div className="row flex-grow">
                    <div className="col-12 stretch-card">
                      <div className="card">
                        <div className="card-body">
                          <h4 className="card-title">Tambah Pekerjaan</h4>
                          <p className="card-description">
                            {" "}
                            Lorem ipsum dolor, sit amet consectetur adipisicing
                            elit. Facilis, laudantium.{" "}
                          </p>
                          <form className="forms-sample">
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Nama Pekerjaan
                              </label>
                              <div className="col-sm-9">
                                <input
                                  type="text"
                                  className="form-control"
                                  value={this.state.name}
                                  onChange={e => {
                                    this.setState({ name: e.target.value });
                                  }}
                                />
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                No SPK / Kontrak
                              </label>
                              <div className="col-sm-9">
                                <input
                                  type="number"
                                  className="form-control"
                                  value={this.state.no_spk}
                                  onChange={e => {
                                    this.setState({ no_spk: e.target.value });
                                  }}
                                />
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Mulai Pekerjaan
                              </label>
                              <div className="col-sm-4">
                                <input
                                  type="date"
                                  className="form-control"
                                  value={this.state.start_time}
                                  onChange={e => {
                                    this.setState({
                                      start_time: e.target.value
                                    });
                                  }}
                                />
                              </div>
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-2 col-form-label"
                              >
                                Selesai Pekerjaan
                              </label>
                              <div className="col-sm-3">
                                <input
                                  type="date"
                                  className="form-control"
                                  value={this.state.deadline}
                                  onChange={e => {
                                    this.setState({ deadline: e.target.value });
                                  }}
                                />
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Pemberi Pekerjaan
                              </label>
                              <div className="col-sm-9">
                                <input
                                  type="text"
                                  className="form-control"
                                  value={this.state.given_by}
                                  onChange={e => {
                                    this.setState({ given_by: e.target.value });
                                  }}
                                />
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Penerima Pekerjaan
                              </label>
                              <div className="col-sm-9">
                              <select
                                  className="form-control"
                                  value={this.state.user_id}
                                  onChange={e => {
                                    this.setState({
                                      user_id: [Number(e.target.value)]
                                    });
                                  }}
                                >
                                  <option>pilih</option>
                                  {this.state.List_user}
                                </select>
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Lokasi Kerja
                              </label>
                              <div className="col-sm-9">
                                <input
                                  type="text"
                                  className="form-control"
                                  value={this.state.job_location}
                                  onChange={e => {
                                    this.setState({
                                      job_location: e.target.value
                                    });
                                  }}
                                />
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Jenis Pekerjaan
                              </label>
                              <div className="col-sm-7">
                                <select
                                  className="form-control"
                                  value={this.state.category_id}
                                  onChange={e => {
                                    this.setState({
                                      category_id: e.target.value
                                    });
                                  }}
                                >
                                  <option>pilih</option>
                                  {this.state.list_category}
                                </select>
                              </div>
                              <div className="col-sm-2">
                                <button
                                  
                                  type="button"
                                  className="btn btn-primary btn-sm"
                                  data-toggle="modal"
                                  data-target="#exampleModalCenter"
                                >
                                  <i className="fa fa-plus" />
                                  Jenis Baru
                                </button>
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              ></label>
                              <div className="col-sm-9">
                                <label htmlFor="" className="col-md-2">
                                  Nilai Material
                                </label>
                                <input
                                  type="text"
                                  className="form-control col-md-4"
                                  value={this.state.nilai_material}
                                  onChange={e => {
                                    this.setState({
                                      nilai_material: e.target.value
                                    });
                                  }}
                                />
                                <label htmlFor="" className="col-md-2">
                                  Nilai Jasa
                                </label>
                                <input
                                  type="text"
                                  className="form-control col-md-4"
                                  value={this.state.nilai_jasa}
                                  onChange={e => {
                                    this.setState({
                                      nilai_jasa: e.target.value
                                    });
                                  }}
                                />
                              </div>
                            </div>
                            <div className="form-group row">
                              <label
                                htmlFor="exampleInputEmail2"
                                className="col-sm-3 col-form-label"
                              >
                                Deskripsi
                              </label>
                              <div className="col-sm-9">
                                <textarea
                                  type="text"
                                  className="form-control"
                                  value={this.state.description}
                                  onChange={e => {
                                    this.setState({
                                      description: e.target.value
                                    });
                                  }}
                                />
                              </div>
                            </div>
                            <button
                              type="submit"
                              className="btn btn-success mr-2"
                              onClick={e => {
                                e.preventDefault();
                                this.addJob();
                              }}
                            >
                              Submit
                            </button>
                            <button type="reset" className="btn btn-light">
                              Cancel
                            </button>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <div
          className="modal fade"
          id="exampleModalCenter"
          tabIndex={-1}
          role="dialog"
          aria-labelledby="exampleModalCenterTitle"
          aria-hidden="true"
        >
          <div
            className="modal-dialog modal-dialog-centered"
            role="document"
          >
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLongTitle">
                  Tambah jenis Pekerjaan
                </h5>
              </div>
              <div className="modal-body">
                <form >
                  <div className="form-group">
                    <label htmlFor="">Nama jenis Pekrjaan</label>
                    <input type="text" className="form-control" required
                    value={this.state.name_jobCa}
                    onChange={e => {
                      this.setState({ name_jobCa: e.target.value });
                    }}
                    />
                  </div>
                  <button type="button" className="btn btn-primary pull-right " 
                  onClick={e => {
                    e.preventDefault();
                      this.postJobCa();
                      }}
                  >
                    <i className="fa fa-save" />
                    Simpan
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    );
  }
}
