import axios from "axios";
import {RootPath} from './url';

const Service = (path, method, data=null) => {
  const promise = new Promise((resolve, reject) => {
    axios({
      baseURL: RootPath,
      method: method,
      url: `/${path}`,
      data: data,
      headers: {
        Authorization:
          "Bearer " + localStorage.getItem('token')
      }
    })
    .then((res)=>{
      resolve(res);
    })
    .catch((err)=>{
      reject(err.response);
    })
  });
  return promise;
};

export default Service;