import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class Menu extends Component {

  setBtnActivate(e) {
    e.target.classList.remove("btn-secondary")
    e.target.classList.add("btn-primary")
  }

  render() {
    return (
      <div>
        <div className="row page-title-header">
          <div className="col-md-12 text-center">
            <div className="page-header-toolbar">
              <div
                className="btn-group toolbar-item"
                role="group"
                aria-label="Basic example"
              >
                <Link to="/Dashboard" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary mr-2">
                  Progress Karyawan
                </Link>
              </div>
              <div className="btn-group toolbar-item">
                <Link to="/ListUser" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary mr-2">
                  List Users
                </Link>
              </div>
              <div className="btn-group toolbar-item">
                <Link to="ListJob" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary mr-2">
                  List Pekerjaan
                </Link>
              </div>
              <div className="btn-group toolbar-item">
                <Link to="PermisionSet" onClick={(e) => {this.setBtnActivate(e)}} className="btn btn-secondary">
                  Hak Akses
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
