import React, { Component } from "react";
import Navbar from "../includes/Navbar";
import Menu from "../includes/Menu";
import Footer from "../includes/Footer";
import API from "../services";

export default class ListUser extends Component {
  constructor() {
    super();
    this.state = {
      data: null,
      
    };
  }
  UNSAFE_componentWillMount() {
    this.getUserList();
  }
  delUser(username){
    console.log(username)
    API.DelUser(username)
    .catch((err)=>{
      console.log(err)
    })
    this.getUserList();
  }
  

  getUserList() {
    API.GetUserList().then(res => {
      console.log(res.data);
      if (res.data) {
        const data = res.data.data.map(item => (
          <tr key={item.id}>
            <td>#</td>
            <td>{item.user_detail.fullname}</td>
            <td>{item.email}</td>
            <td>{item.category_access_name}</td>
            <td>{item.user_detail.address}</td>
            <td>{item.user_detail.phone_number}</td>
            <td>{item.user_detail.work_start_time}</td>
            <td>
              <label className={item.user_detail.activate ? "badge badge-success" : "badge badge-danger"}> {item.user_detail.activate ? "aktive" : "non Aktive"}</label>
            </td>
            <td>
              <button type="button" className="btn btn-warning btn-sm mr-1">
                <i className="fa fa-pencil" />
                edit
              </button>
              <button type="button" className="btn btn-danger btn-sm " onClick={()=>{this.delUser(item.username)}}>
                <i className="fa fa-trash" />
                delete
              </button>
            </td>
          </tr>
        // console.log(item)
        ));
        this.setState({
          data:data
        })
      } else {
        console.log("error");
      }
    });
  }
 

  render() {
    return (
      <div>
        <Navbar />
        <div className="container-fluid page-body-wrapper">
          <div className="container-fluid">
            <div className="content-wrapper">
              <Menu />
              <hr />
              <div className="row">
                <div className="col-lg-12 grid-margin stretch-card mx-auto">
                  <div className="card">
                    <div className="card-body">
                      <div className="float-right mb-3">
                        <a href="AddUser" className="btn btn-primary btn-sm ">
                          <i className="fa fa-plus" />
                          add User
                        </a>
                      </div>
                      <h4 className="card-title">Set Users</h4>
                      <p className="card-description">
                        Lorem ipsum dolor sit amet, consectetur adipisicing
                        elit, sed do eiusmod tempor incididunt ut labore et
                        dolore magna aliqua.{" "}
                      </p>
                      <table className="table table-hover table-responsive">
                        <thead>
                          <tr>
                            <th style={{ width: 50 }}>#</th>
                            <th style={{ width: 300 }}>Full Name</th>
                            <th style={{ width: 200 }}>email</th>
                            <th style={{ width: 100 }}>Kategori akses</th>
                            <th style={{ width: 300 }}>address</th>
                            <th style={{ width: 150 }}>phone number</th>
                            <th style={{ width: 100 }}>work start</th>
                            <th style={{ width: 80 }}>active</th>
                            <th style={{ width: 150 }}>user History</th>
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.data}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
