import React, { Component } from "react";
import Navbar from "../includes/Navbar";
import Menu from "../includes/Menu";
import Footer from "../includes/Footer";
import API from "../services";

export default class AddUser extends Component {

  constructor(props){
    super(props);
    this.state={
      dataPermision:null,
      username : "",
      email: "",
      password: "",
      fullname: "",
      address : "",
      phone_number : "",
      work_start_time: "",
      activate: true,
      category_access_id:""

    }
  }
  UNSAFE_componentWillMount(){
    if (!localStorage.getItem('token')) {
      console.log('belum login');
    }
    this.getPermision()
  }
  getPermision(){
    API.GetPermision().then(result=>{
      // console.log(result);
      if (result.data) {
        const dataPermision=result.data.data.map(item=>(
          <option key={item.id} value={item.id}>{item.name}</option>
        ));
        this.setState({
          dataPermision:dataPermision
        })
      }else{
        console.log('error');
      }
    })
  }

  addUser(){
    let data={
      username : this.state.username,
      email: this.state.email,
      password: this.state.password,
      fullname: this.state.fullname,
      address : this.state.address,
      phone_number : Number(this.state.phone_number),
      work_start_time: this.state.work_start_time.split('-').reverse().join('-'),
      activate: true,
      category_access_id:Number(this.state.category_access_id)
    }
    console.log(data)
    API.AddUser(data).then(res=>{
      this.props.history.push('/ListUser');

    })
    .catch(err => {
        console.log("error");
        console.log(err)
      });
  }

  render() {
    return (
      <div>
        <Navbar />
        <div className="container-fluid page-body-wrapper">
          <div className="container-fluid">
            <div className="content-wrapper">
              <Menu />
              <hr />
              <div className="row">
                <div className="col-10 grid-margin mx-auto">
                  <div className="card">
                    <div className="card-body">
                      <h4 className="card-title">add user</h4>
                      <form className="form-sample">
                        <p className="card-description"> Personal info </p>
                        <div className="row">
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                Full Name
                              </label>
                              <div className="col-sm-9">
                                <input type="text" className="form-control" 
                                value={this.state.fullname}
                                onChange={e => {
                                  this.setState({ fullname: e.target.value });
                                }}
                                />
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                email
                              </label>
                              <div className="col-sm-9">
                                <input type="text" className="form-control"
                                value={this.state.email}
                                onChange={e => {
                                  this.setState({ email: e.target.value });
                                }}
                                 />
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                username
                              </label>
                              <div className="col-sm-9">
                                <input type="text" className="form-control" 
                                value={this.state.username}
                                onChange={e => {
                                  this.setState({ username: e.target.value });
                                }}
                                />
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                password
                              </label>
                              <div className="col-sm-9">
                                <input type="text" className="form-control" 
                                value={this.state.password}
                                onChange={e => {
                                  this.setState({ password: e.target.value });
                                }}
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                address
                              </label>
                              <div className="col-sm-9">
                                <textarea
                                  className="form-control"
                                  cols={30}
                                  rows={2}
                                  value={this.state.address}
                                  onChange={e => {
                                    this.setState({ address: e.target.value });
                                  }}
                                />
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                phone number
                              </label>
                              <div className="col-sm-9">
                                <input type="number" className="form-control"
                                value={this.state.phone_number}
                                onChange={e => {
                                  this.setState({ phone_number: e.target.value });
                                }} />
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                work start
                              </label>
                              <div className="col-sm-9">
                                <input
                                  type="date"
                                  className="form-control"
                                  value={this.state.work_start_time}
                                  onChange={e => {
                                    this.setState({ work_start_time: e.target.value });
                                  }}
                                />
                              </div>
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label">
                                Kategori Akses
                              </label>
                              <div className="col-sm-9">
                                <select className="form-control"
                                value={this.state.category_access_id} onChange={(e)=>{
                                  this.setState({
                                    category_access_id: e.target.value
                                  });
                                }}
                                >
                                  <option >=======Pilih Kategori=======</option>
                                  {this.state.dataPermision}
                                </select>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          
                          <div className="col-md-6">
                            <div className="form-group row">
                              <label className="col-sm-3 col-form-label" />
                              <div className="sm-9 ml-3">
                                <button className="btn btn-success mr-1" 
                                onClick={e => {
                                e.preventDefault();
                                  this.addUser();
                                  }}>
                                  Simpan
                                </button>
                                <button className="btn btn-dark">
                                  Discard
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
