import React, { Component } from "react";
import Navbar from "../includes/Navbar";
import Menu from "../includes/Menu";
import Footer from "../includes/Footer";
// import axios from 'axios';
import API from "../services";

export default class PermisionSet extends Component {
  constructor() {
    super();
    this.state = {
      data: null,
      data_user_job: null,
      name: "",
      add_user: false,
      delete_user: false,
      edit_user: false,
      add_job: false,
      delete_job: false,
      update_job: false,
      show_job: false,
      print_job: false,
      check_job: false,
      service_job: false
    };
  }

  UNSAFE_componentWillMount() {
    this.getPermisionAPI();
    if (!localStorage.getItem("token")) {
      console.log("belum login");
    }
  }

  AddPermission() {
    let data = {
      name: this.state.name,
      add_user: this.state.add_user,
      delete_user: this.state.delete_user,
      edit_user: this.state.edit_user,
      add_job: this.state.add_job,
      delete_job: this.state.delete_job,
      update_job: this.state.update_job,
      show_job: this.state.show_job,
      print_job: this.state.print_job,
      check_job: this.state.check_job,
      service_job: this.state.service_job
    };
    API.AddCA(data)
      .then(res => {
        console.log("sukses");
        this.getPermisionAPI();
      })
      .catch(err => {
        console.log("error");
      });
  }

  SetCAUser(username, ca_id) {
    API.SetCAUser({
      data: {
        "action" : "off"
      },
      params: `${ca_id}/${username}`
    })
  }

  UsersOfJob(ca_id) {
    this.setState({
      data_user_job: null
    });
    API.UsersAJob(ca_id)
      .then(res => {
        const data_user_job = res.data.data.map(item => (
          <div key={`${ca_id}/${item.id}`} className="col-md-1 text-center mb-2">
            <img
              className="img rounded-circle"
              height="80px"
              width="80px"
              src="../assets//images/20950.jpg"
              alt="img"
            />
            <br />
            <label className="badge">{item.username}</label>
            <button
              onClick={()=>{this.SetCAUser(item.username, ca_id)}}
              type="button"
              title="hapus"
              className="btn btn-icons btn-rounded btn-danger"
            >
              <i className="fa fa-trash" />
            </button>
          </div>
        ));
        this.setState({
          data_user_job: data_user_job
        });
      })
      .catch(err => {
        console.log(err);
      });
  }

  getPermisionAPI() {
    API.GetPermision().then(result => {
      const data = result.data.data.map(item => (
        <tr key={item.id}>
          <td></td>
          <td>{item.name}</td>
          <td>
            <label
              className={
                item.add_job ? "badge badge-success" : "badge badge-danger"
              }
            >
              {item.add_job ? "yes" : "no"}
            </label>
          </td>
          <td>
            <label
              className={
                item.update_job ? "badge badge-success" : "badge badge-danger"
              }
            >
              {item.update_job ? "yes" : "no"}
            </label>
          </td>
          <td>
            <label
              className={
                item.delete_job ? "badge badge-success" : "badge badge-danger"
              }
            >
              {item.delete_job ? "yes" : "no"}
            </label>
          </td>
          <td>
            <label
              className={
                item.show_job ? "badge badge-success" : "badge badge-danger"
              }
            >
              {item.show_job ? "yes" : "no"}
            </label>
          </td>
          <td>
            <label
              className={
                item.print_job ? "badge badge-success" : "badge badge-danger"
              }
            >
              {item.print_job ? "yes" : "no"}
            </label>
          </td>
          <td>
            <label
              className={
                item.service_job ? "badge badge-success" : "badge badge-danger"
              }
            >
              {item.service_job ? "yes" : "no"}
            </label>
          </td>
          <td>
            <button
              onClick={() => {
                this.UsersOfJob(item.id);
              }}
              type="button"
              className="btn btn-info btn-sm"
              data-toggle="modal"
              data-target="#exampleModalCenter"
            >
              <i className="fa fa-eye" />
              view
            </button>

            <button type="button" className="btn btn-warning btn-sm ">
              <i className="fa fa-pencil" />
              edit
            </button>
            <button type="button" className="btn btn-danger btn-sm ">
              <i className="fa fa-trash" />
              delete
            </button>
          </td>
        </tr>
      ));

      this.setState({
        data: data
      });
    });
  }

  render() {
    return (
      <div>
        <Navbar />
        <div className="container-fluid page-body-wrapper">
          <div className="container-fluid">
            <div className="content-wrapper">
              <Menu />
              <hr />
              <div>
                <div className="row">
                  <div className="col-lg-9 grid-margin stretch-card mx-auto">
                    <div className="card">
                      <div className="card-body">
                        <h4 className="card-title">Set Permision</h4>
                        <p className="card-description">
                          Lorem ipsum dolor sit amet, consectetur adipisicing
                          elit, sed do eiusmod tempor incididunt ut labore et
                          dolore magna aliqua.{" "}
                        </p>
                        <form className="form-sample">
                          <table className="table table-hover table-striped table-responsive">
                            <thead>
                              <tr>
                                <th style={{ width: 500 }}>Nama Jabatan</th>
                                <th style={{ width: 100 }}>Add</th>
                                <th style={{ width: 100 }}>Update</th>
                                <th style={{ width: 100 }}>Delete</th>
                                <th style={{ width: 100 }}>Show</th>
                                <th style={{ width: 100 }}>Print</th>
                                <th style={{ width: 150 }}>Service</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td>
                                  <input
                                    value={this.state.name}
                                    onChange={e => {
                                      this.setState({
                                        name: e.target.value
                                      });
                                    }}
                                    type="text"
                                    className="form-control"
                                    id="name_new_ca"
                                  />
                                </td>
                                <td>
                                  <div className="form-check form-check-flat">
                                    <label className="form-check-label">
                                      <input
                                        value={this.state.add_job}
                                        onChange={e => {
                                          this.setState({
                                            add_job: e.target.checked
                                          });
                                        }}
                                        id="add_job_new_ca"
                                        type="checkbox"
                                        className="form-check-input"
                                      />
                                      yes <i className="input-helper" />
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <div className="form-check form-check-flat">
                                    <label className="form-check-label">
                                      <input
                                        value={this.state.update_job}
                                        onChange={e => {
                                          this.setState({
                                            update_job: e.target.checked
                                          });
                                        }}
                                        id="update_job_new_ca"
                                        type="checkbox"
                                        className="form-check-input"
                                      />
                                      yes <i className="input-helper" />
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <div className="form-check form-check-flat">
                                    <label className="form-check-label">
                                      <input
                                        value={this.state.delete_job}
                                        onChange={e => {
                                          this.setState({
                                            delete_job: e.target.checked
                                          });
                                        }}
                                        id="delete_job_new_ca"
                                        type="checkbox"
                                        className="form-check-input"
                                      />
                                      yes <i className="input-helper" />
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <div className="form-check form-check-flat">
                                    <label className="form-check-label">
                                      <input
                                        value={this.state.show_job}
                                        onChange={e => {
                                          this.setState({
                                            show_job: e.target.checked
                                          });
                                        }}
                                        id="show_job_new_ca"
                                        type="checkbox"
                                        className="form-check-input"
                                      />
                                      yes <i className="input-helper" />
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <div className="form-check form-check-flat">
                                    <label className="form-check-label">
                                      <input
                                        value={this.state.print_job}
                                        onChange={e => {
                                          this.setState({
                                            print_job: e.target.checked
                                          });
                                        }}
                                        id="print_job_new_ca"
                                        type="checkbox"
                                        className="form-check-input"
                                      />
                                      yes <i className="input-helper" />
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <div className="form-check form-check-flat">
                                    <label className="form-check-label">
                                      <input
                                        value={this.state.service_job}
                                        onChange={e => {
                                          this.setState({
                                            service_job: e.target.checked
                                          });
                                        }}
                                        id="service_job_new_ca"
                                        type="checkbox"
                                        className="form-check-input"
                                      />
                                      yes <i className="input-helper" />
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <button
                                    type="submit"
                                    className="btn btn-primary"
                                    id="btnSaveCa"
                                    onClick={e => {
                                      e.preventDefault();
                                      this.AddPermission();
                                    }}
                                  >
                                    Simpan
                                  </button>
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col-lg-9 grid-margin stretch-card mx-auto">
                    <div className="card">
                      <div className="card-body">
                        <h4 className="card-title">Data Permision</h4>
                        <p className="card-description">
                          Lorem ipsum dolor sit amet, consectetur adipisicing
                          elit, sed do eiusmod tempor incididunt ut labore et
                          dolore magna aliqua.{" "}
                        </p>
                        <table className="table table-hover table-responsive">
                          <thead>
                            <tr>
                              <th>#</th>
                              <th style={{ width: 400 }}>Nama Jabatan</th>
                              <th style={{ width: 100 }}>Add</th>
                              <th style={{ width: 100 }}>Update</th>
                              <th style={{ width: 100 }}>Delete</th>
                              <th style={{ width: 100 }}>Show</th>
                              <th style={{ width: 100 }}>Print</th>
                              <th style={{ width: 100 }}>Service</th>
                              <th style={{ width: 100 }}>Actions</th>
                            </tr>
                          </thead>
                          <tbody>{this.state.data}</tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          className="modal fade bd-example-modal-lg"
          id="exampleModalCenter"
          tabIndex={-1}
          role="dialog"
          aria-labelledby="exampleModalCenterTitle"
          aria-hidden="true"
        >
          <div
            className="modal-dialog modal-dialog-centered modal-lg"
            role="document"
          >
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLongTitle">
                  User Dari Direktor
                </h5>
                <button className="btn btn-primary">
                  <i className="fa fa-plus-circle" />
                  Tambah User
                </button>
              </div>
              <div className="modal-body">
                <div className="col md-12">
                  <div className="row">{this.state.data_user_job}</div>
                </div>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary">
                  Next
                  <i className="fa fa-angle-double-right" />
                </button>
              </div>
            </div>
          </div>
        </div>

        <Footer />
      </div>
    );
  }
}
